@media screen and (min-width: 320px) and (max-width: 767px) {
  .footer {
    text-align: center;
  }
  .footer__wrapper {
    padding-top: 30px;
  }
  .footer__list {
    list-style: none;
    padding: 0;
  }
  .footer__logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer__item:nth-child(2) {
    margin-top: 20px;
    margin-bottom: 30px;
    font-family: getFonts(main);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
  }
  .footer__logo--icon {
    width: 84px;
    height: 30px;
  }
  .footer__link--icon {
    margin-right: 10px;
  }
  .footer__link--icon {
    width: 20px;
    height: 20px;
  }
  .footer__link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: getColor(primary);
  }
  .footer__bottom {
    background-color: getColor(primary);
    width: 100%;
    height: 70px;
  }
  .footer__bottom--color {
    color: getColor(secondary);
    font-family: getFonts(main);
    font-style: normal;
    font-size: 14px;
    line-height: 2;
    padding-left: 52px;
    padding-top: 21px;
    padding-bottom: 21px;
    text-align: left;
  }
  // .footer__item:nth-child(3) {
  // }
}
